:root{
 --dark-color: #171717;
 --secondary-color: #3c948b;
 --offwhite-color: #f4f4f4;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: Arial, Helvetica, sans-serif;
}
img{
  width: 210px;
  height: 295px;
  box-sizing: border-box;
}
.container{
  max-width: 1170px;
  margin: auto;
}
.btn{
  padding: 10px 17px;
  border: 1px solid var(--dark-color);
  background: var(--dark-color);
  color: var(--offwhite-color);
  margin: 1rem 0;
  font-size: 20px;
  cursor: pointer;  
}
.btn:hover{
  background-color: var(--secondary-color);
}
.btn-block{
  display: block;
  width: 100%;
}

.navbar{
  padding: 1rem;
  background: var(--dark-color);;
}
.navbar__nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__brand a{
  font-size: 1.4rem;
  color: #f4f4f4;
  text-decoration: none;
}
.nav__brand a:hover{
  color: #3c948b;
}
.nav__links{
  display:flex;
  list-style: none;
  align-items: center;
}
.links__link a{
  display: block;
  padding: 1rem;
  color:#f4f4f4;
  text-decoration: none;
  margin-left: 0.4rem;
  border: 1px solid var(--dark-color);;
  font-size: 20px;
}
.links__link a:hover{
  color: #3c948b;
  border-bottom: 1px solid #3c948b;
}
.searchbar{
  width: 100%;
  margin: 2rem 0;
}
.searchbar__form > input{
  width: 100%;
  padding: 10px 17px;
  border: 1px solid var(--dark-color);
  background: var(--offwhite-color);
  color: var(--dark-color);
  font-size: 20px;
  outline-width: 0ch;
}
.searchbar__form > input:focus {
  border: 1px solid var(--secondary-color);
  box-shadow: 0 0 1px 1px rgba(60, 148, 139, 0.5);
}
.alert{ 
  font-size: 20px;  
  margin: 1rem 0;
  color: rgb(255, 187, 0);
  font-weight: bold;
}
